import {withRouter} from 'next/router'
import Layout from '../components/MyLayout'

const Content = (props) => (
  <div>
    <h1>{props.router.query.title}</h1>
    <p>This is a sample blog post</p>
  </div>
)

const Page = withRouter((props) => (
  <Layout>
    <Content/>
  </Layout>
))

export default Page



